.user-wrapper {
  all: unset;
  height: 85px;
  width: 300px;
  border-radius: 5px;
  margin: 0 40% 20px; 
  display: grid;
  grid-template-columns: 30% 70%;
  grid-template-rows: 100%;
  background-color: #fdfffc;
  box-shadow: inset 0 0 7px #7e97a6;
}

.user-wrapper:hover {
  cursor: pointer;
  background-color: #7e97a69f;
}

.user-wrapper:focus {
  outline: none;
}

.user-avatar{
  width: 65px;
  height: 65px;
  position: relative;
  float: left;
  margin: 10px 0 0 10px;
  grid-column-start: 1;
}

.user-info {
  grid-column-start: 2;
  margin-top: 10px;
  color: #373640;
}

.user-name{
  text-align: left;
  margin: 0 5px;
}

.user-description{
  margin: 10px 5px;
  font-size: 13px;
  width: 200px;
  overflow: hidden;
}

.user-description-clamp {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.user-link {
  text-decoration: none;
}
